<script setup lang="ts">
import { ref, computed } from 'vue'
import { 
  Building2,
  CheckCircle2,
  TrendingUp,
  DollarSign,
  Award,
  XCircle,
  ArrowRight,
  ArrowLeft
} from 'lucide-vue-next'

const vendors = ref([
  {
    id: "tech-corp",
    name: "TechCorp Solutions",
    category: "Enterprise",
    scores: { technical: 9.2, experience: 8.8 },
    price: "$2.8M",
    priceType: "Fixed",
    timeline: "8 months",
    overallScore: 9.0,
    strengths: ["Strong technical expertise", "Excellent references", "Comprehensive support"],
    weaknesses: ["Higher cost", "Longer timeline"]
  },
  {
    id: "cloudtech",
    name: "CloudTech Inc",
    category: "Mid-Market",
    scores: { technical: 8.5, experience: 8.2 },
    price: "$2.2M",
    priceType: "Fixed",
    timeline: "6 months",
    overallScore: 8.3,
    strengths: ["Competitive pricing", "Fast delivery", "Good technical skills"],
    weaknesses: ["Limited enterprise experience", "Smaller team"]
  },
  {
    id: "datasoft",
    name: "DataSoft Corporation",
    category: "Enterprise",
    scores: { technical: 8.8, experience: 9.1 },
    price: "$3.1M",
    priceType: "Fixed",
    timeline: "10 months",
    overallScore: 8.9,
    strengths: ["Extensive experience", "Strong data capabilities", "Proven track record"],
    weaknesses: ["Most expensive", "Longest timeline"]
  }
])

const evaluatedCount = computed(() => vendors.value.length)
const averageScore = computed(() => 
  (vendors.value.reduce((sum, v) => sum + v.overallScore, 0) / vendors.value.length).toFixed(1)
)
const priceRange = computed(() => {
  const prices = vendors.value.map(v => parseFloat(v.price.replace(/[$,M]/g, '')))
  return `${Math.min(...prices)}M - $${Math.max(...prices)}M`
})

const lowestPrice = computed(() => {
  const prices = vendors.value.map(v => v.price)
  return prices.reduce((min, price) => price < min ? price : min)
})

const highestPrice = computed(() => {
  const prices = vendors.value.map(v => v.price)
  return prices.reduce((max, price) => price > max ? price : max)
})

const averagePrice = computed(() => {
  const prices = vendors.value.map(v => parseFloat(v.price.replace(/[$,M]/g, '')))
  const avg = prices.reduce((sum, price) => sum + price, 0) / prices.length
  return `$${avg.toFixed(1)}M`
})

const getScoreBadgeClass = (score: number) => {
  if (score >= 9) return "status-badge awarded"
  if (score >= 8) return "status-badge evaluation"
  return "status-badge draft"
}

const getScoreLabel = (score: number) => {
  if (score >= 9) return "Excellent"
  if (score >= 8) return "Good"
  return "Fair"
}

const getPricePercentage = (price: string) => {
  const prices = vendors.value.map(v => parseFloat(v.price.replace(/[$,M]/g, '')))
  const min = Math.min(...prices)
  const max = Math.max(...prices)
  const current = parseFloat(price.replace(/[$,M]/g, ''))
  return ((current - min) / (max - min)) * 100
}
</script>

